import React from 'react';

function SectionHeader({ text, iconName }) {

    return (
        <div className="section-header">
            
            <h2><img
                    src={`/icons/${iconName}.svg`}
                    alt={`${iconName} icon`}
                    className="section-icon"
                    // width="1.5 em"
                    // height="1.5em"
                    // line-height="1"
                />
                {text}</h2>
        </div>
    );
}

export default SectionHeader;