import React, { useState } from 'react';
import { Tooltip } from 'react-tooltip';
import 'react-tooltip/dist/react-tooltip.css';
import SectionHeader from './SectionHeader';


function Contact({ title, subtitle, header, buttons }) {
  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    email: '',
    subject: '',
    message: '',
  });

  const copyToClipboard = (text) => {
    const textarea = document.createElement('textarea');
    textarea.value = text;
    document.body.appendChild(textarea);
    textarea.select();
    document.execCommand('copy');
    document.body.removeChild(textarea);
  };

  const handleCopyClick = (text) => {
    copyToClipboard(text);
    // You can add a UI reaction here, such as changing the button text or showing a notification.
    alert(`${text} copied to clipboard!`);
  };

  const handleTelephoneClick = (text) => {
    const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
    if (isMobile) {
      window.location.href = `tel:${text}`;
    } else {
      handleCopyClick(text);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };


  return (
    <section id="contact">
      <div className='contact-container'>

        {/* <Header title={title} subtitle={subtitle} header={header} buttons={buttons} /> */}
        <div className='main-container'>
          <div className='contact-panel'>
            
            <div className='contact-text'>
            <SectionHeader text={"Contact"} iconName={"contact"} />

              <p>
                Have a project idea or work opportunity? Want to connect? Feel free to reach out!
              </p>
              <div className='contact-icons'>
                <button>
                  <a href='https://www.linkedin.com/in/duvu' target='_blank' rel='noopener noreferrer' data-tooltip-content='LinkedIn' data-tooltip-id='linkedIn'>
                    <img src='icons/share.svg' alt='LinkedIn Profile' className='contact-icon' />
                  </a>
                </button>
                <button
                  onClick={() => handleTelephoneClick('+7082447729')}
                  data-tooltip-content='Phone: +7082447729'
                  data-tooltip-id='phone'
                >
                  <img src='icons/phone-call.svg' alt='Phone Number' className='contact-icon' />
                </button>
                <button
                  onClick={() => handleCopyClick('david.abramov.phd@gmail.com')}
                  data-tooltip-content='Email: david.abramov.phd@gmail.com'
                  data-tooltip-id='email'
                >
                  <img src='icons/email.svg' alt='Email' className='contact-icon' />
                </button>
                
              </div>
              <Tooltip id='linkedIn' place='bottom'>
                LinkedIn Profile
              </Tooltip>
              <Tooltip id='phone' place='bottom'>
                Phone Number
              </Tooltip>
              <Tooltip id='email' place='bottom'>
                Email
              </Tooltip>

              <div className='contact-form'>
              {/* Add your form elements here */}
              <form action='https://formsubmit.co/aaca038f95350d8032170f339ee87f74' method='POST'>
                <label>
                  Name:
                  <input type='text' name='name' onChange={handleChange} autoComplete="true"/>
                </label>
                <label>
                  Email:
                  <input type='email' name='email' onChange={handleChange} autoComplete="true"/>
                </label>
                <label>
                  Company:
                  <input type='text' name='company' onChange={handleChange} autoComplete="true"/>
                </label>
                <label>
                  Message:
                  <textarea name='message' onChange={handleChange}></textarea>
                </label>
                <button className="navigation-button" type='submit'>Send Message</button>
              </form>
            </div>

            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Contact;
